const grup = [
    {
        kode:"01",
        value:"Komoditas"
    },
    {
        kode:"02",
        value:"Subkomoditas"
    },
    {
        kode:"03",
        value:"Jenis Penggunaan"
    },
    {
        kode:"04",
        value:"Jenis Barang"
    },
    {
        kode:"05",
        value:"Kategori Barang"
    },
    {
        kode:"06",
        value:"Varians"
    },
    {
        kode:"07",
        value:"Standard Mutu"
    },
    {
        kode:"08",
        value:"Kode HS"
    },
    {
        kode:"09",
        value:"Nama Latin"
    }                                                                                                                                            
]
const arrKomoditas = [
    {
        kode:'01',
        value:'Beras'
    },
    {
        kode:'02',
        value:'Garam'
    },
    {
        kode:'03',
        value:'Ikan'
    },
    {
        kode:'04',
        value:'Daging Lembu'
    },
    {
        kode:'05',
        value:'Gula'
    }                       
]
const defaultfield = [
    {label:'Nomor', key:'no'},    
    {label:'Komoditas', key:'komoditas'},
    {label:'Uraian Barang', key:'urbarang'},
    {label:'Jumlah Barang', key:'jmlbarang'},
    {label:'Satuan Barang', key:'satuan'}
]
const Isi = () =>{
    const barang = useSelector((state)=>state.barang)
    const [items, setItems] = useState([])
    const [jumlah, setJumlah] = useState([])
    useEffect(() => {
        let mapped = barang.map((x)=>x.komoditas)
        let filtered = mapped.filter((v, i, a) => a.indexOf(v) === i)
        setJumlah(filtered)
        setItems(barang)
    },[barang]);  
    let isi
    if(jumlah.length != 0){
        isi = jumlah.map((komoditas)=>{
            const field = Object.keys(structure[komoditas])
            console.log(defaultfield.concat(grup.filter((x)=>field.includes(x.kode)).map((x)=>{
                return(
                    {['label']:x.value, ['key']:x.kode}
                )
            })))
            return(
                <>
                <div className="mb-1">
                <b>Komoditas : </b>{arrKomoditas.filter((x)=>x.kode == komoditas)[0].value}
                </div>
                <CDataTable
                addTableClasses="josss"
                fields={defaultfield.concat(grup.filter((x)=>field.includes(x.kode)).map((x)=>{
                    return(
                        {['label']:x.value, ['key']:x.kode}
                    )
                })).concat([{label:'Action','key':'action'}])}
                items={items.filter((x)=>x.komoditas == komoditas)}
                scopedSlots={{
                    'komoditas':
                    (item,index)=>{
                        return(
                            <td>
                                {arrKomoditas.filter((x)=>x.kode == item.komoditas)[0].value}
                            </td>
                        )
                    },
                    'action':
                    (item,index)=>{
                        return(
                            <td>
                                <CButton className="btn btn-sm btn-warning">Edit</CButton>
                            </td>
                        )
                    },
                    'no':
                    (item, index) =>{
                        return(
                            <td>
                                {index +1}                                
                            </td>
                        )
                    },
                    '02':
                    (item,index) =>{
                        let label = ""
                        if(item['02']){
                            label = item['02'].label
                        }                  
                        return(
                            <td>
                                {label}
                            </td>
                        )
                    },
                    '03':
                    (item,index) =>{
                        let label = ""
                        if(item['03']){
                            label = item['03'].label
                        }                  
                        return(
                            <td>
                                {label}
                            </td>
                        )
                    },
                    '04':
                    (item,index) =>{
                        let label = ""
                        if(item['04']){
                            label = item['04'].label
                        }                  
                        return(
                            <td>
                                {label}
                            </td>
                        )
                    },
                    '05':
                    (item,index) =>{
                        let label = ""
                        if(item['05']){
                            label = item['05'].label
                        }                  
                        return(
                            <td>
                                {label}
                            </td>
                        )
                    },
                    '06':
                    (item,index) =>{
                        let label = ""
                        if(item['06']){
                            label = item['06'].label
                        }                  
                        return(
                            <td>
                                {label}
                            </td>
                        )
                    },
                    '07':
                    (item,index) =>{
                        let label = ""
                        if(item['07']){
                            label = item['07'].label
                        }                  
                        return(
                            <td>
                                {label}
                            </td>
                        )
                    },
                    '08':
                    (item,index) =>{
                        let label = ""
                        if(item['08']){
                            label = item['08'].label
                        }                  
                        return(
                            <td>
                                {label}
                            </td>
                        )
                    }, 
                    '09':
                    (item,index) =>{
                        let label = ""
                        if(item['09']){
                            label = item['09'].label
                        }                  
                        return(
                            <td>
                                {label}
                            </td>
                        )
                    },                                                                                                                                        
                }}
                />
                </>
            )
        })
    } else {
        isi = "Kosong"
    }
    return(
        <div className="d-flex flex-column">
            {isi}
        </div>
    )
}

export default Isi