import { CButton, CCard, CCardBody, CDataTable, CFormGroup, CInput, CLabel, CModal, CModalBody, CModalHeader, CRow, CSelect, CCol, CTextarea, CModalFooter, CCardHeader } from '@coreui/react'
import { Data } from 'phaser'
import {React, useState} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Isi from './isi'
import Khusus from './Khusus'

const Ujicoba = () =>{
    const umum = useSelector((state)=>state.umum)
    const khusus = useSelector((state)=>state.khusus)
    const barang = useSelector((state)=>state.barang)
    const dispatch = useDispatch()
    const [komoditas, setKomoditas] = useState()
    const [modal, setModal] = useState(false)
    const [data, setData] = useState({})
    const [items, setItems] = useState([])
    const handleSimpan = () =>{
        let copyumum = {...umum}
        let copykhusus = {...khusus}
        let gabungan = {...copyumum, ...copykhusus}
        let barangcopy = barang.slice()
        barangcopy.push(gabungan)
        setModal(false)
        setData({...data, ['komoditas']:"00"})
        setKomoditas("00") 
        dispatch({type: 'set', umum:{
            "urbarang":"",
            "jmlbarang":"",
            "satuan":""
        } })
        dispatch({type: 'set', barang:barangcopy })
    }   
    const handleChange = (x) =>{
        if(x.target.name == "komoditas"){
            setKomoditas(x.target.value)  
            dispatch({type: 'set', khusus:{} })
            dispatch({type: 'set', umum:{
                komoditas:x.target.value,
                urbarang:"",
                jmlbarang:"",
                satuan:""
            }})
            setData({...data, [x.target.name]:x.target.value})
            console.log(umum, khusus)          
        } else {
            setData({...data, [x.target.name]:x.target.value})
            let copy = {...umum, [x.target.name]:x.target.value}
            dispatch({type: 'set', umum:copy })
            console.log(umum, khusus)            
        }
    }
    return(
        <>
        <CModal
        show={modal}
        size="xl"
        onClose={()=>{
            setModal(false)
            setData({...data, ['komoditas']:"00"})
            setKomoditas("00")  
            dispatch({type: 'set', umum:{
                "urbarang":"",
                "jmlbarang":"",
                "satuan":""
            } })        
        }}
        >
            <CModalHeader closeButton>
                <h5>Tambah Data Bahan Baku</h5>
            </CModalHeader>
            <CModalBody>
                <CRow>
                    <CCol md="2">
                        <CFormGroup>
                            <CLabel>Komoditas</CLabel>
                            <CSelect value={data.komoditas} name="komoditas" onChange={(x)=>handleChange(x)} >
                                <option value="00"></option>
                                <option value="01">Beras</option>
                                <option value="02">Garam</option>
                                <option value="03">Hasil Perikanan</option>
                                <option value="04">Daging Lembu</option>
                                <option value="05">Gula</option>
                            </CSelect>
                        </CFormGroup>
                    </CCol>
                </CRow>
                <CCard style={{borderRadius:'12px', boxShadow:'0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%)'}}>
                    <CCardBody>
                        <Khusus modal={modal} komoditas={komoditas} />
                    </CCardBody>
                </CCard>
                <CRow>
                    <CCol md="12">
                        <CFormGroup>
                            <CLabel>Uraian Barang</CLabel>
                            <CTextarea value={umum.urbarang} name="urbarang" onChange={(x)=>handleChange(x)}/>
                        </CFormGroup>
                    </CCol>
                </CRow>
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Jumlah Barang</CLabel>
                            <CInput value={umum.jmlbarang} name="jmlbarang" onChange={(x)=>handleChange(x)} type="number"/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Satuan Barang</CLabel>
                            <CSelect value={umum.satuan} name="satuan" onChange={(x)=>handleChange(x)}>
                                <option value=""></option>
                                <option value="Ton">Ton</option>
                                <option value="Kgm">Kgm</option>
                            </CSelect>
                        </CFormGroup>
                    </CCol>                    
                </CRow>                
            </CModalBody>
            <CModalFooter>
                <div className="d-flex justify-content-end">
                    <div onClick={()=>handleSimpan()} className="mr-3"><CButton className="btn btn-success">Simpan</CButton></div>
                    <div><CButton className="btn btn-danger">Batal</CButton></div>
                </div>
            </CModalFooter>
        </CModal>
        <CCard>
            <CCardHeader><h5>Simulasi Referensi Bahan Baku</h5></CCardHeader>
            <CCardBody>
                <div className="d-flex flex-row justify-content-end mr-3 mb-2">
                    <CButton onClick={()=>setModal(true)} className="btn btn-sm btn-success">Tambah</CButton>
                </div>
                <CCard style={{borderRadius:'12px'}}>
                    <CCardBody className="">
                        <Isi/>                       
                    </CCardBody>
                </CCard>
            </CCardBody>
        </CCard>
        </>
    )
}

export default Ujicoba